<template>
  <div class="token_box">
     <div class="center_box">
       <p class="token_title large_text">Check out token balance</p>
       <div class="token_input">
         <a-input-search
             v-model:value="maxSearchValue"
             placeholder=""
             style="width: 640px"
             size="large"
             allowClear
             @search="onMaxSearch"
         />
       </div>
       <p class="token_text small_text">Recognize all operations, deploy mint transfer.</p>
       <div class="card_box">
         <div class="card_top">
           <p class="middle_text">The full list of tokens </p>
          </div>
         <div class="card_segmented">
           <a-segmented v-model:value="type" :options="options" @change="onSegmented" size="large"/>
           <a-button class="middle_round_btn" type="primary" shape="round" @click="showModal">Deploy</a-button>
         </div>
         <a-table
            style="width: 1078px; overflow-x: auto"
             :columns="columns"
             :data-source="data"
             :customRow="handleClickRow"
             :loading="tableLoading"
             :pagination="pagination"
             @change="handleTableChange"
             row-class-name="table-striped">
           <template #bodyCell="{ column, text }">
             <div v-if="column.dataIndex === 'name'" class="list_item_boxA">
               <p>{{ text }}</p>
             </div>
             <div v-if="column.dataIndex === 'time'" class="list_item_boxB">
               <p>{{dayjs(text).format('YYYY-MM-DD HH:mm:ss')}}</p>
             </div>
             <div v-if="column.dataIndex === 'progress'" class="list_item_boxB">
               <p>{{text + '%'}}</p>
               <a-progress style="width: 210px" strokeColor="#5AE86F" status="active" :percent="text" :show-info="false"/>
             </div>
             <div v-if="column.dataIndex === 'transactions'" class="list_item_boxC">
               <p>{{text}}</p>
               <img style="width: 20px;height: 20px" src="@/assets/img/right_outlined.svg" alt="">
             </div>
           </template>
         </a-table>
       </div>
     </div>

    <a-modal centered width="600px" v-model:open="open" title="Deploy">
      <a-form
          class="form_box"
          ref="formRef"
          :model="formState"
          :rules="rules"
          :label-col="{ style: { width: '150px' } }"
          labelAlign="left"
      >
        <div class="form_box_top">
          <p>Protocol</p>
          <span class="ant-form-text">{{formState.protocol}}</span>
        </div>

        <a-form-item label="Tick" name="tick">
          <a-input type="text" v-model:value="formState.tick" size="large" placeholder="Identifier of the token, like ORDI"/>
        </a-form-item>

        <a-form-item label="Total Supply" name="totalSupply">
          <a-input v-model:value="formState.totalSupply" size="large" placeholder="21000000"/>
        </a-form-item>

        <a-form-item label="Limit Per Mint" name="limitPerMint">
          <a-input v-model:value="formState.limitPerMint" size="large" placeholder="1000"/>
        </a-form-item>

      </a-form>

      <template #footer>
        <a-button style="margin-right: 8px" key="back" size="large" @click="handleCancel">Cancel</a-button>
        <a-button key="submit" type="primary" size="large" :loading="loading" @click="onSubmit">Deploy</a-button>
      </template>
    </a-modal>
  </div>
</template>

<script>
import { defineComponent, reactive, ref, toRaw, toRefs,onMounted, computed } from 'vue';
import { useRouter, useRoute, onBeforeRouteLeave } from "vue-router";
import {  notification } from 'ant-design-vue';
import {getTokens,stringToHex} from '@/utils/api'
import { useStore } from 'vuex';

export default defineComponent({
  name: "Tokens",
  setup() {
    const Router = useRouter();
    const formRef = ref();
    const store = useStore();
    const dayjs = require('dayjs')
    const formState = reactive({
      protocol: 'script',
      tick: '',
      totalSupply: '',
      limitPerMint: '',
      mintFee:'',
    });

    const rules = {
      protocol: [{ required: true}],
      tick: [{ required: true, message: 'Please enter a maximum of 4 characters',max:4,min:4,trigger: 'change', }],
      totalSupply: [{ required: true, message: 'Please enter a positive integer', trigger: 'change', }],
      limitPerMint: [{ required: true, message: 'Please enter a positive integer',trigger: 'change', }],
    }

    const Data = reactive({
      maxSearchValue: '',
      minSearchValue: '',
      type: 'All',
      options: ['All','In-progress', 'Completed'],
      tableLoading: true,
      page:1,
      columns: [
        {title: 'Token', dataIndex: 'name', key: 'name', width: 212, ellipsis: true,},
        {title: 'Deploy Time', dataIndex: 'time', key: 'time', width: 262, },
        {title: 'Progress', dataIndex: 'progress', key: 'progress', width: 262, },
        {title: 'Holders', dataIndex: 'holders', key: 'holders', width: 162, },
        {title: 'Transactions', dataIndex: 'transactions', key: 'transactions', width: 162, },
      ],
      data: [],
      loading: false,
      open: false,
    });

    onMounted(() => {
      Methods.loadTokens()
    })
    const Methods = {
      loadTokens() {
        Data.page=1
        Data.tableLoading=true
        var type = 0
        if(Data.type=="In-progress")type=1;
        if(Data.type=="Completed")type=2;
        getTokens(type,Data.page).then((response) => {
          Data.tableLoading=false
          if(response.code==0){
            Data.data = response.data.list
          }else{
            Data.tableLoading=false
            notification["error"]({message:"Error",description:response.msg});
          }
        })
      },
      onMaxSearch() {
        if(!!Data.maxSearchValue) {
          Router.push({ path: 'Search', query: { address: Data.maxSearchValue }})
        }
      },
      showModal() {
        Data.open = true;
      },
      onSegmented(value) {
        Data.page = 0
        Methods.loadTokens()
      },
      onMinSearch() {

      },
      handleTableChange() {

      },
      handleClickRow(record, index) {
        return {
          onClick: (event) => { Router.push({ path: 'Info', query: { tick: record.name }})},
        }
      },
      onSubmit() {
        formRef.value.validate().then( async () => {
          if(formState.tick.length!=4){
             window.notification["error"]({message:"Error",description:'Tick maximum of 4 characters'});
             return
          }
          var reg = /^[\d]+$/;
          if(!reg.test(formState.totalSupply.trim())){
             window.notification["error"]({message:"Error",description:'TotalSupply only numbers can be entered'});
             return
          }
          if(!reg.test(formState.limitPerMint.trim())){
             window.notification["error"]({message:"Error",description:'Limit only numbers can be entered'});
             return
          }
          if(!store.state.web3){
            store.dispatch("connectWallet")
            window.notification["error"]({message:"Error",description:"Connect your wallet"});
            return;
          }
          let tickExist = await store.state.contract.methods.checkTick(formState.tick.toUpperCase().trim()).call();
          if(tickExist){
            window.notification["error"]({message:"Error",description:'Tick name unavailable'});
            return
          }
          Data.loading = true;
          let json = stringToHex('{"p":"'+formState.protocol+'","op":"deploy","tick":"'+formState.tick.toUpperCase().trim()+'","amt":"'+formState.totalSupply+'","limit":"'+formState.limitPerMint+'","mintAmt":"0"}');
          window.ethereum.request({
              method: 'eth_sendTransaction',
              params: [{from:store.state.wallet_address,to:store.state.toAddress,data:json}],
            }).then((txHash) => {
              window.notification["success"]({message:"",description:"Deploy success"});
              Data.open = false
              formRef.value.resetFields();
            }).catch((error) =>  {
              Data.loading = false
            });
        }).catch(error => {
            window.notification["error"]({message:"Error",description:error});
        });
      },
      handleCancel() {
        Data.open = false;
        formRef.value.resetFields();
      }
    };
    return {
      formRef,
      formState,
      ...toRefs(Data),
      ...Methods,
      rules,
      dayjs
    }
  }
});
</script>

